import React, { useEffect, useState } from 'react';

const ExpertsSection = () => {
  const [expertsVisible, setExpertsVisible] = useState(false); // Control experts section visibility
  const [hasScrolledToExperts, setHasScrolledToExperts] = useState(false);

  const expertsData = [
    {
      name: "John Doe",
      profession: "Software Engineer",
      expertise: "Python",
      picture: "images/Experts/John Doe.webp",
      rating: 4,
    },
    {
      name: "Jane Smith",
      profession: "Data Scientist",
      expertise: "Machine Learning",
      picture: "images/Experts/Jane Smith.webp",
      rating: 4,
    },
    {
      name: "Michael Johnson",
      profession: "Baker",
      expertise: "Pastry",
      picture: "images/Experts/Michael Johnson.webp",
      rating: 4.5,
    },
    {
      name: "Emily Williams",
      profession: "Digital Marketer",
      expertise: "Social Media Marketing",
      picture: "images/Experts/Emily Williams.webp",
      rating: 3,
    },
    {
      name: "Robert Lee",
      profession: "Mechanic",
      expertise: "Automotive Repair",
      picture: "images/Experts/Robert Lee.webp",
      rating: 3.5,
    },
    {
      name: "Sarah Adams",
      profession: "Gardener",
      expertise: "Landscape Design",
      picture: "images/Experts/Sarah Adams.webp",
      rating: 5,
    },
    {
      name: "David Thompson",
      profession: "Musician",
      expertise: "Piano",
      picture: "images/Experts/David Thompson.webp",
      rating: 4,
    },
    {
      name: "Jennifer Brown",
      profession: "Chef",
      expertise: "French Cuisine",
      picture: "images/Experts/Jennifer Brown.webp",
      rating: 4,
    },
    {
      name: "William Green",
      profession: "Photographer",
      expertise: "Portrait Photography",
      picture: "images/Experts/William Green.webp",
      rating: 4.5,
    },
    {
      name: "Linda Martinez",
      profession: "Artist",
      expertise: "Oil Painting",
      picture: "images/Experts/Linda Martinez.webp",
      rating: 3.5,
    },
    // Add more experts here as needed
  ];

   // Shuffle the experts array and select three random experts
   const getRandomExperts = () => {
    const shuffledExperts = [...expertsData];
    for (let i = shuffledExperts.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [shuffledExperts[i], shuffledExperts[j]] = [shuffledExperts[j], shuffledExperts[i]];
    }
    return shuffledExperts.slice(0, 3);
  };

  const renderExpertTiles = () => {
    const selectedExperts = getRandomExperts();

    return selectedExperts.map((expert, index) => (
      <div className="col-md-6 col-lg-4" key={index}>
        <div className="feature-block expert-tile">
          <img src={expert.picture} alt={expert.name} className="img-fluid expert-picture" />
          <h4 className="mt-4">{expert.name}</h4>
          <p>{expert.profession}</p>
          <p>Expertise: {expert.expertise}</p>
          <a href="#">View Profile <i className="fas fa-comment" aria-hidden="true"></i></a>
          {getStarRatingHTML(expert.rating)}
        </div>
      </div>
    ));
  };

  const getStarRatingHTML = (rating) => {
    const roundedRating = Math.round(rating * 2) / 2;
    const fullStars = Math.floor(roundedRating);
    const hasHalfStar = roundedRating % 1 !== 0;

    const starElements = [];
    for (let i = 0; i < fullStars; i++) {
      starElements.push(
        <i key={i} className="fas fa-star" style={{ color: "#6f42c1" }}></i>
      );
    }

    if (hasHalfStar) {
      starElements.push(
        <i key="half" className="fas fa-star-half-alt" style={{ color: "#6f42c1" }}></i>
      );
    }

    const emptyStars = 5 - Math.ceil(roundedRating);
    for (let i = 0; i < emptyStars; i++) {
      starElements.push(
        <i key={`empty-${i}`} className="far fa-star" style={{ color: "#e3e3e3" }}></i>
      );
    }

    return <div className="star-rating">{starElements}</div>;
  };

  useEffect(() => {
    // Call the function to generate random experts when the component loads
    getRandomExperts();
  }, []);

  const showExpertsSection = () => {
    setExpertsVisible(true);

    // Scroll to the experts section if not scrolled before
    if (!hasScrolledToExperts) {
      const scrollOptions = {
        top: expertsSectionRef.current.offsetTop - 230, // Adjust the value to stop a bit earlier
        behavior: "smooth",
      };
      window.scrollTo(scrollOptions);
      // Set the flag to true after scrolling once
      setHasScrolledToExperts(true);
    }
  };

  return (
    <section id="experts" className={`experts padd-section text-center${expertsVisible ? ' visible' : ''}`}>
      <div className="container">
        <div className="row text-center">
          <h4 className="fw-bold lead mb-3">Experts who can help you</h4>
          <div className="heading-line"></div>
          <p className="fw-light">Discover knowledgeable professionals across various fields ready to assist with your questions and challenges.</p>
        </div>
      </div>

      <div className="container">
        <div className="row justify-content-center">
          {renderExpertTiles()}
        </div>
      </div>
    </section>
  );
};

export default ExpertsSection;





